    saveSociety = () => {
        this.setState({loading: true});
        let capitalHistory = [];
        capitalHistory.push({
            nbActions:this.state.sCapital.totalNbActions,
            nominalValue:(parseInt(this.state.sCapital.totalCapital) / parseInt(this.state.sCapital.totalNbActions)).toString()
        });

        firebase.database().ref('society/' + localStorage.getItem('uid') + 'Suisse').set({
            dateCreation: moment().format("YYYY-MM-DD"),
            sName: this.state.sName,
            sBut: this.state.sBut,
            representant: this.state.representant,
            sCapital: {
                capitalHistory:capitalHistory,
                totalCapital: this.state.sCapital.totalCapital,
                totalNbActions: this.state.sCapital.totalNbActions,
                defaultNominalValue: (parseInt(this.state.sCapital.totalCapital) / parseInt(this.state.sCapital.totalNbActions)).toString(),
                lastNominalValue: (parseInt(this.state.sCapital.totalCapital) / parseInt(this.state.sCapital.totalNbActions)).toString()
            },
            type_societe: "SARL",
            sSiege: this.state.sSiege,

            nbGerant: parseInt(this.state.sActionnairePhy.length),
            nbAssociate: parseInt(this.state.sActionnaireMoral.length),

            sAdministrator: this.state.sActionnairePhy,
            sAssociate: this.state.sActionnaireMoral,

            expertSmartCo: this.state.expertSmartCo,
            domiciAdress: this.state.domiciAdress,
            mondatAdmin: this.state.mondatAdmin,

        }).then(res => {
            setTimeout(() => {
                this.setState({loading: false});
                this.openSnackbar('success', "La cration de votre nouvelle entreprise est effectue avec succs");
                setTimeout(() => {
                    this.props.history.push("/creation/SARL/" + localStorage.getItem("uid") + "Suisse/details")
                }, 1500);
            }, 1500)

        }).catch(err => {
            console.log(err);
        })


    };

    async addCIN1(image) {
        let file = image.target.files[0];

        firebase.storage().ref("/users/files/").child(file.name).put(file)
            .then((snapshot) => {
                snapshot.ref.getDownloadURL().then(url => {

                    firebase.database().ref('society/' + localStorage.getItem("uid") + "Suisse").update({
                        'CIN1RespTokenFile': {
                            "url": url,
                            "name": file.name,
                            "size": file.size
                        },
                    }).then(res => {
                        this.setState({
                            cin1: {
                                url: url,
                                name: file.name,
                                size: file.size
                            }
                        })
                    }).catch(err => {
                        console.log(err);
                    });
                })

            }).catch((error) => {
            console.log('One failed:', file.name, error.message)
        });

    }

